@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  color: #444;
}


.animals{width:100%}.animals__filter-section{display:flex;flex-wrap:wrap;grid-gap:20px;gap:20px;justify-content:center;margin-bottom:26px}.animals__filter-buttons{cursor:pointer;padding:2px 10px;font-size:20px;font-weight:600;text-transform:capitalize;border-bottom:2px solid #444;transition:all 0.5s}.animals__filter-buttons--active{color:#28a745;border-bottom-color:#28a745}.animals__animal-card-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(30%, 1fr));grid-gap:16px;gap:16px;margin-bottom:56px}.animals__add-animal-button-wrapper{display:flex;justify-content:center}

.modal__background{position:fixed;display:flex;justify-content:center;align-items:center;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:rgba(0,0,0,0.4)}.modal__window{position:relative;padding:25px;border:1px solid #EBEBEB;box-shadow:0 4px 15px rgba(44,44,44,0.05);border-radius:12px;background-color:#fff;width:100%;max-width:500px}.modal__contents{display:grid;grid-template-columns:1fr;grid-row-gap:36px;row-gap:36px}

.form{display:grid;grid-template-columns:1fr;grid-row-gap:48px;row-gap:48px}.form__header{display:flex;justify-content:space-between;align-items:center}.form__content{display:grid;grid-template-columns:1fr;grid-row-gap:24px;row-gap:24px}.form__form-field{display:grid;grid-template-columns:1fr;grid-row-gap:4px;row-gap:4px;grid-column-gap:6px;-webkit-column-gap:6px;column-gap:6px}.form__text-input{padding:5px 10px;border:1px solid #333;border-radius:4px;font-size:16px}.form__text-input:focus-visible{border:1px solid #80bdff;outline:none;box-shadow:0 0 0 2px rgba(0,123,255,0.25)}.form__select{text-transform:capitalize}.form__select-option{text-transform:capitalize}.form__footer{display:flex;justify-content:right;grid-gap:12px;gap:12px;line-height:1}.form__error-message-wrapper{min-height:20px}.form__error-message-text{font-size:12px;color:#dc3545}

.button{display:flex;justify-content:center;align-items:center;padding:5px 10px;font-size:16px;font-weight:500;color:#fff;background-color:#28a745;border:1px solid transparent;border-radius:4px;cursor:pointer}.button:hover{background-color:#259a40}.button:active{background-color:#228c3a}.button--secondary{background-color:#fff;border:1px solid #28a745;color:#28a745}.button--secondary:hover{background-color:#fff;border:1px solid #28a745;color:#28a745;outline:#28a745 solid 1px}.button--secondary:active{box-shadow:0 0 2px 1px #818181}.button--icon{padding:0;background-color:#fff;font-size:24px}.button--icon path{color:#28a745}.button--icon:hover{background-color:#fff;border:1px solid transparent;-webkit-transform:scale(1.05);transform:scale(1.05)}.button--icon:active{-webkit-transform:scale(1);transform:scale(1)}

.animal-card{display:grid;grid-template-columns:1fr;grid-template-rows:1fr auto;grid-row-gap:8px;row-gap:8px;border:1px solid transparent;border-radius:4px;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.animal-card__image{width:100%;height:100%;max-height:300px;object-fit:cover;border-top-left-radius:4px;border-top-right-radius:4px}.animal-card__info-section{display:flex;justify-content:center;flex-direction:column;grid-gap:8px;gap:8px;padding:5px;place-self:end center}.animal-card__name{font-size:22px;text-align:center;text-transform:capitalize}.animal-card__species{color:#999;font-size:14px;text-align:center;text-transform:capitalize}

.header{display:grid;grid-template-columns:-webkit-min-content 1fr auto;grid-template-columns:min-content 1fr auto;grid-column-gap:36px;-webkit-column-gap:36px;column-gap:36px;justify-items:center;align-items:center;padding:0 30px}.header__logo{max-width:110px}.header__navigation-wrapper{height:100%;justify-self:left}.header__navigation{height:100%;display:flex;align-items:center}.header__navigation-link{display:flex;align-items:center;padding:0 20px;text-decoration:none;color:#444;font-size:20px;height:100%}.header__navigation-link--active{background-color:#28a745;color:#fff}.header__language-selection{display:flex;grid-gap:6px;gap:6px;align-items:center}.header__select-language{padding:2px 4px;border:1px solid #333;border-radius:4px;font-size:16px;text-transform:uppercase}.header__language-option{text-transform:uppercase}.header-wrapper{max-width:1000px;padding:0;border-bottom:2px solid #28a745;margin:0 auto 20px}.content-wrapper{max-width:1000px;margin:0 auto;padding:30px}

.translations-page-heading{margin-bottom:16px}.animal-translations{width:100%;border-collapse:collapse;border:1px solid #444;margin-bottom:36px}.animal-translations th,.animal-translations td{border:1px solid #444;padding:4px 12px}.animal-translations td[class=animal-translations__animal-name--input]{padding:0}.animal-translations__language-code{text-transform:uppercase}.animal-translations__animal-name{text-transform:capitalize}.animal-translations__animal-name-input{padding:4px 12px;border:none;font-size:16px;color:#444;width:100%;text-transform:capitalize;line-height:1.2}.animal-translations__animal-name-input:focus-visible{outline:1px solid #28a745;box-shadow:0 0 0 1px #28a745 inset}.animal-translations__submit-button-wrapper{display:flex;justify-content:right}

